.product-card{
    border: 1px solid #E4E7EC;
    box-shadow: 0px 0px 1px 1px rgba(228, 231, 236, 0.15);
    border-radius: 6px;
    padding: 30px 15px 15px;
}

.product-card {
    max-width: 300px;
    margin: 0 auto;
}

.product-card-img{
    text-align: center;
}

.product-card p{
    font-weight: 400;
    font-size: 12px;
    line-height: 26px;
    color: #576071;
    padding-top: 24px;
}

.product-card h4{
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #323A4B;
    padding-top: 5px;
}

.stars{
    display: flex;
    gap: 1px;
    padding-top: 10px;
}

.stars i{
    color: #DFDEDC;
}

.price{
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    align-items: center;
}

.price h3{
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: #006D77;
}

.price a{
    padding: 6px 15px;
    color: #006D77;
    background: #BEE1E6;
    border-radius: 6px;
    text-decoration: none;
}

.product-card-img{
    position: relative;
    height: 150px;
}

.product-card-img p{
    position: absolute;
    padding: 3px 15px;
    background: #101828;
    border-radius: 25px;
    font-size: 10px;
    color: #fff;
}