.mini-cards-section{
    display: flex;
    gap: 30px;
    justify-content: space-between;
    align-items: center;
    padding: 0 120px;
}

.mini-card{
    display: flex;
    gap: 5px;
    border: 1px solid #E4E7EC;
    box-shadow: 0px 0px 1px 1px rgba(228, 231, 236, 0.15);
    border-radius: 6px;
    padding: 30px 20px;
    max-width: 350px;
}

.mini-card-content h6{
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #323A4B;
}

.mini-card-content p{
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    color: #101828;
}

@media screen and (max-width: 1150px) {
    .mini-cards-section{
        flex-direction: column;
        padding: 0px;
    }
}