.products-section-header{
    text-align: center;
    margin: 0 auto;
    max-width: 500px;
    padding-bottom: 60px;
}

.products-section-header h1{
    color: #101828;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
}

.products-section{
    padding: 120px 120px;
}

.products-section-cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.products-section .products-section-cards{
    margin-top: 20px;
}

#products-section-cards-first{
    margin-top: 0;
}

@media screen and (max-width: 1150px) {

    .products-section{
        padding: 0;
    }

    .products-section-header{
        padding-bottom: 20px;
        padding: 30px;
    }

    .products-section-header h1{
        font-size: 30px;
    }

    .products-section-cards{
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

}