.categories-section{
    padding: 40px 120px;
}

.categories-section-header h1{
    color: #101828;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
}

.categories-section-header p{
    color: #475467;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    padding-top: 25px;
}

.categories-section-header{
    text-align: center;
    margin: 0 auto;
    max-width: 525px;
}

.categories-layout{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.left-card-preference{
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    align-items: center;
}

.categories-layout-left-card{
    border: 1px solid #D1DAE8;
    box-shadow: 0px 3px 6px rgba(219, 220, 222, 0.17);
    border-radius: 6px;
    padding: 40px 30px;
    width: 525px;
    max-width: 525px;
}

.categories-layout-left-card h2{
    font-weight: 600;
    font-size: 30px;
    line-height: 26px;
    color: #323A4B;
}


.left-card-preference-name p{
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #667085;
}

.left-card-preference-number{
    padding: 10px;
    color: #006D77;
    background: #E4E7EC;
    border-radius: 50px;
}

.categories-layout-left-card .line{
    background: #E4E7EC;
    width: 100%;
    height: 1px;
}

.categories-layout-right-cards-group{
    display: flex;
    gap: 30px;
}

.categories-layout-right-cards-block{
    display: flex;
    gap: 15px;
    align-items: center;
    padding: 25px 20px;
    border: 1px solid #D1DAE8;
    border-radius: 6px;
    width: 350px;
}

.categories-layout-right-cards-block p{
    font-weight: 600;
    font-size: 14px;
    line-height: 26px;
    color: #323A4B;
}

.categories-layout-right-cards{
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.categories-layout-right-cards-exploremore{
    display: flex;
    justify-content: center;
}

.categories-layout-right-cards-exploremore .categories-layout-right-cards-block{
    padding: 12px 60px;
    text-align: center;
    width: unset;
}

@media screen and (max-width: 1550px) {
    .categories-layout-right-cards-block{
        width: unset;
    }
}

@media screen and (max-width: 1150px) {
    .categories-layout{
        flex-direction: column;
        gap: 50px;
    }

    .categories-section{
        padding: 40px;
    }

    .categories-layout-left-card{
        width: unset;
        margin-top: 25px;
    }

    .categories-layout-right-cards-group{
        flex-direction: column;
    }
}