.hero-section{
    display: flex;
    justify-content: space-between;
    gap: 120px;
    padding: 40px 120px;
    align-items: center;
}

.hero-section-content{
    max-width: 600px;
}

.hero-section-content h4{
    font-weight: 600;
    font-size: 25px;
    line-height: 26px;
    color: #98A2B3;
}

.hero-section-content h1{
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
    color: #101828;
    margin-top: 28px;
}

.hero-section-content p{
    letter-spacing: 0.045em;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #475467;
    padding-top: 30px;
    padding-bottom: 40px;
}

.hero-section-content-buttons{
    display: flex;
    gap: 15px;
}

.hero-section-content-buttons a{
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #101828;
    border-radius: 4px;
    padding: 12px 20px;
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 5px;
}

.hero-section-content-buttons a:first-child{
    background: #101828;
    color: #fff;
}

.hero-section-content-preferences{
    display: flex;
    gap: 60px;
    margin-top: 70px;
}

.hero-section-content-preferences .preference{
    display: flex;
    align-items: center;
    gap: 10px;
}

.hero-section-content-preferences .preference p{
    padding: 0;
    font-size: 16px;
    line-height: 26px;
    font-weight: 500;
    color: #66728B;
    padding-top: 5px;
}

.hero-section-content-preferences .preference h4{
    color: #101828;
    font-weight: 600;
    font-size: 30px;
    line-height: 26px;
}

.hero-section-image{
    position: relative;
}

.hero-section-image .blue-ellipse-small{
    position: absolute;
    right: 0;
}

.hero-section-image .pink-ellipse-small{
    position: absolute;
    bottom: 0;
}

.hero-section-image .graph{
    position: absolute;
    right: -100px;
    bottom: -75px;
}

@media screen and (max-width: 1150px) {
    .hero-section{
        flex-direction: column;
    }

    .hero-section-image img{
        width: 100%;
    }

    .hero-section-image .pink-ellipse{
        display: none;
    }
}