.cross-border-section{
    padding: 40px 120px;
}

.cross-border-section-header{
    text-align: center;
    max-width: 650px;
    margin: 0 auto;
}

.cross-border-section-header p{
    color: #E29578;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    padding: 8px 27px;
    background: #FFF3EA;
    border-radius: 48px;
    max-width: 200px;
    margin: 0 auto;
}

.cross-border-section-header h1{
    margin-top: 40px;
    font-weight: 600;
    font-size: 50px;
    line-height: 60px;
    color: #101828;
    padding-bottom: 60px;
}

.cross-border-section-card{
    text-align: center;
    background: #FFF3EA;
    border-radius: 8px;
    padding: 45px 60px;
    width: 285px;
    height: 320px;
}

#cross-border-section-card2{
    background: #FDE2E4;
}

#cross-border-section-card3{
    background: #DBECE5;
}

#cross-border-section-card4{
    background: #DBECE5;
}

#cross-border-section-card5{
    background: #D1ECFD;
}

.cross-border-section-card h2{
    color: #1E2534;
    font-weight: 500;
    font-size: 32px;
    line-height: 26px;
    margin-top: 20px;
}

.cross-border-section-card p{
    color: #667085;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    padding-top: 35px;
}

.cross-border-section-cards{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

#cross-border-section-cards2{
    display: flex;
    justify-content: center;
    margin-top: 120px;
    gap: 60px;
}

.b-line{
    position: absolute;
    right: 180px;
    top: 0px;
}

@media screen and (max-width: 1150px) {
    .b-line, .line{
        display: none;
    }

    .cross-border-section-cards, #cross-border-section-cards2{
        flex-direction: column;
        gap: 25px;
        margin-top: 25px;
    }

    .cross-border-section{
        padding: 40px;
    }

    .cross-border-section-header h1{
        font-size: 30px;
        padding-bottom: 0;
    }
}